import React from "react";
import PropTypes from "prop-types";
import MaterialTable from "material-table";
import { Avatar, Typography } from "@material-ui/core";
import { KeyboardArrowDown, KeyboardArrowUp } from "@material-ui/icons";

const Delta = ({ value, id }) => {
  if (!value) return null;

  if (value === "UP")
    return (
      <KeyboardArrowUp
        data-testid={`Delta__up-pos${id}`}
        style={{ color: "forestgreen" }}
      />
    );
  return (
    <KeyboardArrowDown
      data-testid={`Delta__down-pos${id}`}
      style={{ color: "red" }}
    />
  );
};

Delta.propTypes = {
  value: PropTypes.oneOf(["UP", "DOWN"]),
  id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired
};

Delta.defaultProps = {
  value: null
};

const Position = ({ showDeltas, showPositions, column }) => {
  return (
    <Typography
      variant="body1"
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column"
      }}
    >
      {showDeltas && <Delta value={column.delta} id={column.position} />}
      {showPositions && column.position}
    </Typography>
  );
};

Position.propTypes = {
  showDeltas: PropTypes.bool.isRequired,
  showPositions: PropTypes.bool.isRequired,
  column: PropTypes.shape({
    delta: PropTypes.oneOf(["UP", "DOWN"]),
    position: PropTypes.number.isRequired
  }).isRequired
};

const buildPositions = (showPositions, showDeltas) => {
  if (!(showPositions || showDeltas)) {
    return null;
  }

  return {
    title: "Position",
    field: "position",
    cellStyle: {
      width: "10px",
      textAlign: "center"
    },
    render: col => {
      const props = {
        showPositions,
        showDeltas,
        column: col
      };
      return <Position {...props} />;
    }
  };
};

const getColumns = (extraCols, config) => {
  return [
    buildPositions(config.showPositions, config.showDeltas),
    {
      title: "Name",
      field: "name",
      render: col => {
        return (
          <div style={{ display: "flex", alignItems: "center" }}>
            <Avatar alt={col.name} src={col.image} />
            <Typography variant="body2" style={{ paddingLeft: "5px" }}>
              {col.name}
            </Typography>
          </div>
        );
      }
    },
    ...extraCols.map(col => ({
      ...col,
      cellStyle: {
        ...col.cellStyle,
        textAlign: "end"
      },
      headerStyle: {
        ...col.headerStyle,
        textAlign: "end"
      }
    })),
    {
      title: config.valueName,
      field: "value",
      cellStyle: {
        textAlign: "right",
        ...config.valueCellStyle
      },
      headerStyle: {
        textAlign: "right",
        ...config.valueHeaderStyle
      }
    }
  ]
    .filter(Boolean)
    .map(col => ({
      ...col,
      cellStyle: {
        ...col.cellStyle,
        padding: "5px 15px"
      }
    }));
};

const sortRankings = data => {
  const sortedData = data
    .concat()
    .sort((a, b) => {
      if (a.value > b.value) return -1;
      if (a.value < b.value) return 1;
      return 0;
    })
    .map((d, i) => ({ ...d, position: i + 1 }));
  return sortedData;
};

const Leaderboard = props => {
  const { title, data, columns, config, onRowClick, noDataMessage } = props;

  /** Merge default config with config specified in props */
  const mergedConfig = { ...Leaderboard.defaultProps.config, ...config };

  return (
    <MaterialTable
      data={sortRankings(data)}
      columns={getColumns(columns, mergedConfig)}
      title={title}
      onRowClick={onRowClick}
      options={{
        showTitle: !!title,
        showFirstLastPageButtons: false,
        showSelectAllCheckbox: false,
        search: false,
        headerStyle: {
          padding: "5px 15px",
          position: "sticky",
          top: 0
        },
        maxBodyHeight: mergedConfig.maxBodyHeight,
        sorting: mergedConfig.columnSorting,
        draggable: false,
        paging: false,
        toolbar: !!title
      }}
      localization={{
        body: {
          emptyDataSourceMessage: noDataMessage
        }
      }}
    />
  );
};

Leaderboard.propTypes = {
  /** Message to display when no prop `data` is specified. `Default: No data to display` */
  noDataMessage: PropTypes.string,
  /** Title for the leaderboard. If not title defined, heading will be hidden */
  title: PropTypes.string,
  /** Extra configuration for the leaderboard */
  config: PropTypes.shape({
    /** Change the default header display text for the sorted value */
    valueName: PropTypes.string,
    /** Maximum body height for the leaderboard */
    maxBodyHeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    /** Styles for `value` column cell */
    valueCellStyle: PropTypes.shape({}),
    /** Styles for `value` column header */
    valueHeaderStyle: PropTypes.shape({}),
    /** Enable sorting of columns. `Default: false` */
    columnSorting: PropTypes.bool,
    /** Displays cardinal numbers for positions. `Default: true` */
    showPositions: PropTypes.bool,
    /** Displays icons indicating previous positions. `Default: false` */
    showDeltas: PropTypes.bool
  }),
  /** Datasource for the leaderboard. An array of objects with a required `value` property for sorting
   * but can have extra properties.
   */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      value: PropTypes.number.isRequired,
      image: PropTypes.string,
      delta: PropTypes.oneOf(["UP", "DOWN", "NONE"])
    }).isRequired
  ),
  /** Extra columns you want to add to leaderboard. They inherit `value`'s cell styles */
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      field: PropTypes.string,
      cellStyle: PropTypes.shape({}),
      headerStyle: PropTypes.shape({})
    })
  ),
  /** To handle row click (event, rowData) */
  onRowClick: PropTypes.func
};

Leaderboard.defaultProps = {
  noDataMessage: "No data to display",
  title: null,
  data: [],
  columns: [],
  config: {
    valueName: "Value",
    maxBodyHeight: "100%",
    columnSorting: false,
    showPositions: true,
    showDeltas: false
  },
  onRowClick: () => {}
};

export default Leaderboard;