import React, { useState } from 'react';
import { post } from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { FormGroup, Input, Button, Form } from "reactstrap";
import { useAuth0, withAuthenticationRequired } from "@auth0/auth0-react";
import Loading from "../components/Loading";
import Highlight from "../components/Highlight";
import configs from "../configs";
import "./ExternalApi.css";
import 'react-dropzone-uploader/dist/styles.css'
import Dropzone from 'react-dropzone-uploader'

export const FileUpload = () => {
  const [state, setState] = useState({
    showResult: false,
    apiMessage: null,
    error: null,
    file: null,
    values: null,
    score: null
  });

  const { getIdTokenClaims } = useAuth0();

  const onFormSubmit = async() => {
    //e.preventDefault() // Stop form submit
    fileUpload(state.file);
    toast("File submitted!", {
      type: "success",
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    })
  };

  const handleChangeStatus = ({ meta, file }, status) => { console.log(status, meta, file) }

  const onChange = ({ meta, file }, status) => {
    if(file['type'] != 'application/json')
    {
      toast("Please upload a valid json file", {
        type: "error",
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
    else if (file['type'] > 10000)
    {
      toast("Your file is too large", {
        type: "error",
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
    else {
      setState({...state, file:file})
    }
  };

  const fileUpload = async(file) => {
    try {
      const claims = await getIdTokenClaims();
      const token = claims.__raw;
      const url = configs.API_URL;
      const formData = new FormData();
      formData.append('file',file)
      const config = {
          headers: {
            'Authorization': `Bearer ${token}`,  
            'content-type': 'multipart/form-data'
          }
      }
      const responseData = await post(url, formData, config)
      if(responseData["data"]["error"])
      {
        toast("Error returned from the server. Are you sure you formatted correctly?", {
          type: "warning",
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      }
      setState({...state, error:responseData["data"]["error"], values:responseData["data"]["data"], score:responseData["data"]["score"]});
    } catch (err) {
      setState({...state, error:err});
    }
  };

  const getUploadParams = () => {
    return {url: 'https://httpbin.org/post'}
  }

  return (
    <>
      <ToastContainer />
      <div className="mb-5">
        <h1 className='primary'>File Upload</h1>
        <hr/>
        <Form>
          <FormGroup>
            <Dropzone
              getUploadParams={getUploadParams}
              onChangeStatus={onChange}
              onSubmit={onFormSubmit}
              styles={{ dropzone: { minHeight: 300, minWidth: 300} }}
              maxFiles={1}
              accept="application/json"
              inputContent={(files, extra) => (extra.reject ? 'Json airplane file only!' : 'Drop your file')}
              styles={{
                dropzoneReject: { borderColor: 'red', backgroundColor: '#DAA' },
                inputLabel: (files, extra) => (extra.reject ? { color: 'red' } : {}),
              }}
            />
{/*             <Input 
            type="file"
            onChange={(e) => onChange(e)}
            id="custom-file-translate-scss" 
            label="Select a file"
            lang="en"
            data-browse="Button text"
            /> */}
            <Button onClick={(e) => onFormSubmit(e)} className="mdl-button mdl-js-button" variant="outline-primary">Upload</Button>
          </FormGroup>
        </Form>
        {state.error && (
          <div className="mt-5">
            <h4 className="muted">Error</h4>
            <Highlight>
              {JSON.stringify(state.error, null, 2)}
            </Highlight>
          </div>
        )}  
        {!state.error && state.score && (
          <div className="mt-5">
            <h4 className="muted">Your Score</h4>
            <Highlight language="json">
              {JSON.stringify(state.score, null, 2)}
            </Highlight>
          </div>
        )}
        {!state.error && state.values && (
          <div className="mt-5">
            <h4 className="muted">Values</h4>
            <Highlight language="json">
              {JSON.stringify(state.values, null, 2)}
            </Highlight>
          </div>
        )}
      </div>
    </>
  );
};

export default withAuthenticationRequired(FileUpload, {
  onRedirecting: () => <Loading />,
});
